@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
    transform: translateY(45%);
    width: 100%;
}
.app-wrapper {
    background-color: #fff;
    min-width: 50px;
}
 .title {
    color: #f80759;
    text-align: center;
}

.form-container {
    max-width: 40%;
    margin: auto;
}

.name,
.email,
.password {
    margin: 20px;
    color: #969292;
    font-size: 16px;
}
.password {
    margin-bottom: 40px;
}

.input {
    height: 32px;
    font-size: 20px;
    outline: none;
    width: 100%;
    border-style: none none solid none;
    margin-bottom: 2px;
} 

 .submit {
    display: flex;
    text-transform: uppercase;
    font-size: 20px;
    border-radius: 25px;
    color: white;
    padding:  10px 60px;
    background: linear-gradient(to right, #ab1dcf, #f80759);
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
     rgba(0, 0, 0, 0.22) 0px 15px 12px;
    margin: auto;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.1s;
}

button:active {
    transform: translateY(2px);
}

.forms-success {
    display: flex;
    justify-content: center;
    margin-top: 250px;
    min-width: 370px;
    color: #f80759;
}

.please-field {
    color: red;
}
body {
    margin: 0;
    padding: 0;
}

.header-container {
    position: fixed;
    top: 0;
    z-index: 999;
    width: 100%;
}

.body-container {
    transform: translateY(8%);
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.main-nav{
    width: 100%;
    height: 5rem;
    display: flex;
    background-color: rgb(240, 233, 238);
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
}

.header-left{
    display: flex;
}


.logo {
    display: grid;
     /* background-color: blueviolet; */
    grid-column: 1/3;
    justify-content: start;
    align-items: center;
    font-size: 22px;
    margin-bottom: 3px;
    margin-left: 10px;
}

.menu-link {
    height: 5rem;
    width: 15rem;
    margin: 1px;
}

.menu-link ul {
    height: 3rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 10px;
} 

.signup-login{
    height: 2rem;
    width: 12rem;
    padding-right: 20px;
}

.signup-login button {
    padding: 10px 15px;
    font-size: 16px;
    border-radius: 20px;
    background-color: #0057ff;
    margin-left: 8px;
    color: cornsilk;
    border: none;
    outline: none;
}

.card-container {
    width: 400px;
    height: 300px;
    margin: 20px;
    position: relative;
    
}

.card-container h3 {
    margin: 0px;
}

.card-container p {
    margin: 0px;
}

.card-container img{
    width: 100%;
    height:90%;
    border-radius: 15px;

}
.body-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.saveBtn {
    position: absolute;
    top: 10px;
    left: 10px;
    border-radius: 8px;
    outline: none;
    border: none;
    background-color:rgba(37, 36, 36, 0.6);
    color:white;
    padding: 8px;
    display: none;
}

.card-container:hover .saveBtn {
    display: block;
}

